// @mixin   
@mixin size-content{
    max-width: $widthContent; 
 }
 // @mixin  

//   h2
 @mixin h2-list {
    font-size: 2.57rem;
    font-family: 'Overpass';
    font-weight: bold;
    line-height: 51px;
    text-align: center;
 }
//   h3
 @mixin h3-list{
    font-family: 'Overpass';
    font-size: 1.625rem;
    line-height: 31px;
    letter-spacing: 0.1px;
    text-align: center;
 }

//   h4
@mixin h4-list{
   line-height: 22px;
   font-family: 'Overpass';
   font-size: 1.1rem;
   text-align: center;
}
//  
 @mixin content-p{
    line-height: 22px;
 }
// inputs
@mixin registrationi-input {
   width: inherit;
   outline: none;
   border-style: initial;
   padding: 10px 0 18px 13px;
   font-size: 0.95rem;
}
// fieldset
@mixin registration-fieldset {
   border-radius: 4px;
   border-color: aliceblue;
   margin: 0;
}